#richtext-container p {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

#richtext-container a {
  color: #e0cea9;
  font-size: 1.125rem;
  line-height: 1.75rem;
  text-decoration: underline;
}
#richtext-container a:hover {
  color: #abc9f3;
}

#richtext-container h1 {
  font-size: 3.75rem; /* 60px */
  line-height: 1;
}

#richtext-container h2 {
  font-size: 3rem; /* 48px */
  line-height: 1;
}

#richtext-container h3 {
  font-size: 2.25rem; /* 36px */
  line-height: 2.5rem; /* 40px */
}

#richtext-container h4 {
  font-size: 1.875rem; /* 30px */
  line-height: 2.25rem; /* 36px */
}

#richtext-container h5 {
  font-size: 1.5rem; /* 24px */
  line-height: 2rem; /* 32px */
}

#richtext-container h6 {
  font-size: 1.25rem; /* 20px */
  line-height: 1.75rem; /* 28px */
}

#richtext-container li {
  list-style-type: disc;
  margin-left: 1rem;
}

#richtext-container blockquote {
  background: #1f1f1f;
  border-radius: 0.25rem;
  border-left: 10px solid #e0cea9;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  quotes: "\201C" "\201D" "\2018" "\2019";
}
#richtext-container blockquote:before {
  color: #bdbdbd;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}
#richtext-container blockquote p {
  display: inline;
}

#richtext-container img {
  border-radius: 0.25rem;
  border: 4px solid #1f1f1f;
  margin: 0rem auto;
}

.carousel-container,
.carousel-container div {
  height: 500px !important;
}
